<style>
@import url(./assets/main.css);
</style>
<script>
import {utilities} from "./assets/main.js";
export default{
  created(){
    document.addEventListener('scroll',utilities.toggleScrolled);
    document.addEventListener('scroll', utilities.toggleScrollTop);
    window.addEventListener('load', utilities.toggleScrolled);
    window.addEventListener('load', utilities.toggleScrollTop);
  },
  unmounted(){
    document.removeEventListener('scroll',utilities.toggleScrolled)
    document.removeEventListener('scroll', utilities.toggleScrollTop);
    window.remoteEventListener('load', utilities.toggleScrolled)
    window.removeEventListener('load', utilities.toggleScrollTop);
  },
}
</script>