import { createRouter, createWebHashHistory } from 'vue-router';

import IndexSite from "./views/IndexSite.vue";
import ContactUs from "./views/ContactUs.vue";
import HistorySite from './views/HistorySite.vue';
import PageNotFound from "./views/PageNotFound.vue";
import OurTeam from './views/OurTeam.vue';
import FAQ from './views/FAQ.vue';

const routes = [
    {
        path: "/",
        name: "inicio",
        component: IndexSite
    },
    {
        path: "/Contactanos",
        name: "contactanos",
        component: ContactUs
    },
    {
        path: "/Historia",
        name: "historia",
        component: HistorySite
    },
    {
        path:"/NuestroEquipo",
        name: "equipo",
        component: OurTeam
    },
    {
        path: "/FAQ",
        name: "faq",
        component: FAQ
    },
    {
        path: "/:pathMatch(.*)*",
        name: "No encontrada",
        component: PageNotFound
    }
];

const router = createRouter({
    history: createWebHashHistory(),
    routes: routes
})
export default router;